import React from 'react';
import {BaseHeaderProps, Header, HeaderTag, FontWeight} from '@spinglobal/react-foundations';
import {header__primary} from './header-primary-styles';

export function HeaderPrimary({style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    return <Header level={HeaderTag.Heading1} fontWeight={FontWeight.Bolder} style={{...header__primary, ...style}} {...headerProps}/>
}
