import React, {useState} from 'react';
import {faFire, faHome, faStickyNote, faWrench, IconDefinition} from '@fortawesome/free-solid-svg-icons'
import {HeaderBold} from '../../Components';
import {
    Action,
    Borderer,
    BordererPresets,
    BorderStyle,
    BreakpointExtraLarge,
    BreakpointLarge,
    BreakpointMedium,
    BreakpointSmall,
    FlexDirection,
    Flexer,
    FlexerPresets,
    FlexMainAxis,
    FlexSecondaryAxis,
    FlexWrap,
    Hover,
    Icon,
    Paragraph,
    Shadower,
    Surface,
    TextAlign,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {Modal} from '../../Components/Modal';
import {HeaderSecondary} from '../../Components/Headers/HeaderSecondary';
import {font__voicePrimary} from '../../styles';

export function ExpertiseLogo({icon}: { icon: IconDefinition }) {
    return <Surface width={120} height={120} background='#d64d57' style={{
        ...BordererPresets.P50,
        ...FlexerPresets.Center
    }}>
        <Icon fill='white' size={48} icon={icon}/>
    </Surface>;
}

export interface ExpertiseCardProps {
    icon: IconDefinition;
    title: string;
}

const EXPERTISE_CARD_DEFAULT_STYLES = {width: 520, height: 320, fontSize: 24, marginTop: 120}
const EXPERTISE_CARD_STYLES = {
    [BreakpointSmall]: {...EXPERTISE_CARD_DEFAULT_STYLES, width: '90%', fontSize: 18, marginTop: 120},
    [BreakpointMedium]: {...EXPERTISE_CARD_DEFAULT_STYLES, fontSize: 20, marginTop: 120},
    [BreakpointLarge]: {...EXPERTISE_CARD_DEFAULT_STYLES, width: 460, height: 282, fontSize: 20},
    [BreakpointExtraLarge]: {...EXPERTISE_CARD_DEFAULT_STYLES}
}

export function ExpertiseCard({icon, title}: ExpertiseCardProps) {
    const [open, setOpen] = useState<boolean>(false);
    const bp = useBreakpoint();
    const {width, height, fontSize, marginTop} = EXPERTISE_CARD_STYLES[bp];

    return <Action onClickCapture={() => setOpen(true)}>
        <Surface width={width} height={height} background='#e9b543'
                 style={{
                     marginTop,
                     minWidth: 360,
                     ...Shadower({voffset: 30, blur: 90, color: '#d64d57'}),
                     ...FlexerPresets.Center
                 }}>
            <Modal background='rgba(0, 0, 0, .8)' open={open} onClickCapture={() => {
                setOpen(false)
            }}>
                <ExpertiseLogo icon={icon}/>
            </Modal>
            <ExpertiseLogo icon={icon}/>
            <HeaderBold
                text={title}
                textAlign={TextAlign.Center}
                style={{
                    marginTop: '18px',
                    color: 'white',
                    fontSize
                }}
            />
            <Hover hoverStyles={{backgroundColor: '#d64d57'}}>
                <Surface width={96} height={42} style={{
                    ...Borderer({
                        borderWidth: 1,
                        borderStyle: BorderStyle.Solid,
                        borderColor: '#bc8458',
                        borderRadius: 4
                    }),
                    ...Shadower({hoffset: 1, voffset: 1, blur: 2}),
                    ...FlexerPresets.Center
                }}>
                    <Paragraph text='Learn More' style={{...font__voicePrimary, color: 'white', userSelect: 'none'}}/>
                </Surface>
            </Hover>
        </Surface>
    </Action>;
}

const SERVICE_EXPERTISE_DEFAULT_PROPS = {
    direction: FlexDirection.Row,
    margin: 0
};

const SERVICE_EXPERTISE_PROPS = {
    [BreakpointSmall]: {...SERVICE_EXPERTISE_DEFAULT_PROPS, direction: FlexDirection.Column},
    [BreakpointMedium]: {...SERVICE_EXPERTISE_DEFAULT_PROPS, direction: FlexDirection.Column},
    [BreakpointLarge]: {
        ...SERVICE_EXPERTISE_DEFAULT_PROPS,
        margin: '0 120px 60px 120px',
    },
    [BreakpointExtraLarge]: {
        ...SERVICE_EXPERTISE_DEFAULT_PROPS,
        margin: '0 120px 60px 120px',
    }
}

export function ServicesExpertise() {
    const bp = useBreakpoint();
    const {direction, margin} = SERVICE_EXPERTISE_PROPS[bp];
    const paragraph1 = 'AC Disaster Consulting is a full-spectrum emergency management firm. Our team of experts provide high-quality consulting services in the following areas:';
    return <Surface background='#0794c0'
                    style={FlexerPresets.ColumnCenter}>
        <HeaderSecondary text='Service & Expertise' textAlign={TextAlign.Center} style={{margin: '48px 0 12px 0'}}/>
        <Paragraph text={paragraph1} textAlign={TextAlign.Center}
                   style={{...font__voicePrimary, color: 'white', maxWidth: 720}}/>
        <Surface width='100%' style={{
            maxWidth: 1400,
            margin: margin,
            ...Flexer({
                direction,
                main: FlexMainAxis.SpaceAround,
                secondary: FlexSecondaryAxis.Center,
                wrap: FlexWrap.Wrap
            })
        }}>
            <ExpertiseCard icon={faFire} title={'PLANNING & PREPAREDNESS'}/>
            <ExpertiseCard icon={faStickyNote} title={'RESPONSE OPERATIONS & LOGISTICS'}/>
            <ExpertiseCard icon={faHome} title={'RECOVERY'}/>
            <ExpertiseCard icon={faWrench} title={'MITIGATION'}/>
        </Surface>
    </Surface>
}
