import React, {PropsWithChildren} from 'react';
import {
    ComponentProps,
    FlexerPresets,
    Hover,
    Image,
    Paragraph,
    PositionBlock,
    PositionTypes,
    ShadowerPresets,
    Surface
} from '@spinglobal/react-foundations';
import {font__voicePrimary, font__voicePrimaryLeadin} from '../../styles';
import {ColorSet} from '../../styles';

export interface EmployeeCardProps extends ComponentProps {
    src: string;
    name: string;
    title: string;
    bio: string;
    education: Array<string>;
    experience: string;
    ask: Array<string>;
    ln?: string;
}

const employee_card__header = {
    ...font__voicePrimaryLeadin,
    fontSize: 24
}

function EmployeeCardSurface({children, ...props}: PropsWithChildren<{ width?: number, classes?: Array<string>, height?: number }>) {
    return <Surface {...props}
                    style={{
                        overflow: 'hidden',
                        position: 'relative',
                        margin: 24,
                        ...ShadowerPresets.Blur16,
                        ...FlexerPresets.Column
                    }}>
        {children}
    </Surface>;
}

function MainInfoSurface({name, title, bio}: { name: string, title: string, bio: string }) {
    const marginTop = 12;
    const padding = 12;
    const style = {marginTop, padding, ...FlexerPresets.Column};
    return <Surface style={style}>
        <Paragraph text={name} style={{...font__voicePrimaryLeadin}}/>
        <Paragraph text={title} style={{...font__voicePrimary, fontSize: 18, color: ColorSet.Neutral2, marginTop}}/>
        <Paragraph text={bio} style={{...font__voicePrimary, color: ColorSet.Neutral2, marginTop}}/>
    </Surface>;
}

function EmployeeBulletSection({data, heading, num}: { data: Array<string>, heading: string, num: number }) {
    const paddingLeft = 24;
    const marginTop = num !== 0 ? 12 : 0;
    return <Surface>
        <Paragraph text={heading} style={{...employee_card__header, marginTop}}/>
        <ul style={{paddingLeft}}>
            {data.map(e => <li key={e} style={font__voicePrimary}>{e}</li>)}
        </ul>
    </Surface>
}

function EmployeeCardBackSurface({width, height, education, experience, ask}: { width: number | string, height: number | string, education: string[], experience: string, ask: string[] }) {
    return <Surface width={width} height={height} background='white'
                    style={{borderBottomRightRadius: 2, borderBottomLeftRadius: 2, padding: 24}}>
        <EmployeeBulletSection heading='Education' num={0} data={education}/>
        <EmployeeBulletSection heading='Past Experience:' num={1} data={[experience]}/>
        <EmployeeBulletSection heading='Ask Me About:' num={2} data={ask}/>
    </Surface>
}

export function EmployeeCard({src, name, title, bio, experience, ask, education, ln}: EmployeeCardProps) {
    const width = 330;
    const height = 600
    return <Hover subStyles={{' > div:nth-of-type(2)': {left: 0}}}>
        <EmployeeCardSurface width={width} height={height}>
            <Image width={width} src={src} alt='CEO'/>
            <MainInfoSurface name={name} title={title} bio={bio}/>
            <PositionBlock position={PositionTypes.Absolute} left={-width}
                           style={{transition: 'all .07s ease-out'}}>
                <EmployeeCardBackSurface width={width} height={height} education={education} experience={experience} ask={ask}/>
            </PositionBlock>
        </EmployeeCardSurface>
    </Hover>;
}
