import React from 'react';
import {BaseHeaderProps, Header, HeaderTag} from '@spinglobal/react-foundations';
import {header__bold} from './header-bold-styles';

export function HeaderBold({style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    return <Header level={HeaderTag.Heading1} style={{...header__bold, ...style}} {...headerProps}/>
}
