import React, {CSSProperties, PropsWithChildren} from 'react';
import {HeaderPrimary} from '../../Components';
import CONTENT from './about';
import {
    BordererPresets, BreakpointExtraLarge,
    BreakpointLarge,
    BreakpointMedium, BreakpointSmall,
    FlexDirection,
    Flexer,
    FlexerPresets,
    FlexMainAxis,
    Image,
    Paragraph,
    Shadower,
    Surface,
    TextAlign,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {font__voicePrimary, font__voicePrimaryLeadin} from '../../styles';

function AboutParagraph({style = {}, text}: { style?: CSSProperties, text: string }) {
    const marginBottom = useBreakpoint() <= BreakpointMedium ? 36 : 0;

    return <Paragraph
        text={text}
        textAlign={TextAlign.Center}
        style={{
            ...font__voicePrimary,
            ...style,
            padding: '0 18px',
            marginBottom,
            display: 'inline'
        }}/>
}

const ABOUT_IMAGE_DEFAULT_PROPS = {
    width: 'initial',
    marginTop: 0
};

const ABOUT_IMAGE_PROPS = {
    [BreakpointSmall]: {...ABOUT_IMAGE_DEFAULT_PROPS, marginTop: 12, width: '90%'},
    [BreakpointMedium]: {...ABOUT_IMAGE_DEFAULT_PROPS, marginTop: 12, width: '90%'},
    [BreakpointLarge]: {...ABOUT_IMAGE_DEFAULT_PROPS, width: 540},
    [BreakpointExtraLarge]: {...ABOUT_IMAGE_DEFAULT_PROPS}
}

function AboutImage() {
    const bp = useBreakpoint();
    const {width, marginTop} = ABOUT_IMAGE_PROPS[bp];

    return <Surface width={width} classes={['about-image']} style={{margin: '0 auto', alignSelf: 'start'}}>
        <Image src='https://acdisaster.com/img/Alyssa-carrier-acdisaster-surveying-damage.jpg'
               alt='Recovering From Disaster'
               width={width}
               style={{
                   display: 'block',
                   margin: `${marginTop} auto`,
                   ...BordererPresets._8,
                   ...Shadower({
                       voffset: 4,
                       spread: 4,
                       blur: 8,
                       color: 'rgba(0, 0, 0, .25)'
                   })
               }}/>
    </Surface>
}

function AboutRow({children, style}: PropsWithChildren<{ style?: CSSProperties }>) {
    const flexer = useBreakpoint() <= BreakpointMedium ? FlexerPresets.ColumnCenter : FlexerPresets.MainCenter;
    return <Surface style={{...style, padding: 12, ...flexer}}>
        {children}
    </Surface>;
}

function AboutSection(obj: {leadin: string, text: string}) {
    const {leadin, text} = obj;
    return <Surface style={{textAlign: 'center', margin: 24}}>
        <span style={{...font__voicePrimaryLeadin, marginRight: -14}}>{leadin}</span>
        <AboutParagraph text={text}/>
    </Surface>
}

export function AboutUs() {
    return <Surface width='100%' style={FlexerPresets.Center}>
        <HeaderPrimary text='Our Mission' style={{margin: '48px 0 24px 0'}}/>
        <AboutRow style={{marginRight: 18}}>
            <Surface style={Flexer({direction: FlexDirection.Column, main: FlexMainAxis.SpaceAround})}>
                {CONTENT[0].map(e => <AboutSection key={e.leadin} {...e}/>)}
            </Surface>
            <AboutImage/>
        </AboutRow>
        <AboutRow>
            {CONTENT[1].map(e => <AboutSection key={e.leadin} {...e}/>)}
        </AboutRow>
    </Surface>;
}
