function SpinLogo() {
    const spin__logo: CSSProperties = {
        position: 'absolute',
        top: '40vh',
        left: 'calc(50vw - 30vw/2)',
        userSelect: 'none'
    }
    return <Image src='./spin_logo.png' alt='SPIN Logo' width='30vw' style={spin__logo}/>
}

export function ParallaxScreen() {
    useStyle('html', {fontSize: FontConstants.RootFontSize});
    const [height, setHeight] = useState<number>(0);
    const [scrollPct, setScrollPct] = useState<number>(0);
    const screenRef = useRef<HTMLDivElement>(null);

    const range1 = genRange(-5, 250, 250);
    const range2 = genRange(-90, 90);
    const range3 = genRange(5, 300, 50);

    useEffect(() => {
        const handler = () => screenRef.current && setHeight(screenRef.current.offsetHeight);
        handler();
        window.addEventListener('resize', handler);
        return () => window.removeEventListener('resize', handler);
    }, []);

    useEffect(() => {
        const handler = () => setScrollPct(calcScrollPct(height, window.scrollY));
        window.addEventListener('scroll', handler);
        return () => window.removeEventListener('scroll', handler);
    });

    // prevent flashes
    // @ts-ignore
    const ref = useAnonymousStyle({visibility: 'initial !important'})
    return <div style={{visibility: 'hidden', overflow: 'hidden', position: 'relative'}} className={ref.current}>
        <Surface height='100vh' width='100%'>
            <div ref={screenRef} style={{width: '100%', height: '100%', overflow: 'hidden'}}>
                <SpinLogo/>
                <PositionBlock
                    top={.75 * range1(scrollPct) + 'vh'}
                    left='5vw'
                    position={PositionTypes.Relative}
                >
                    <RotatingGradient
                        colors={colors1}
                        style={{
                            ...BordererPresets.P50,
                            ...Shadower({blur: 2 * range3(scrollPct)}),
                            ...Transformer({rotate: '30deg'})
                        }}
                        width={range3(scrollPct) + 'vw'}
                        height={range3(scrollPct) + 'vw'}
                        stepSize={25}
                    />
                </PositionBlock>
                <PositionBlock
                    top={75 - .1 * range1(scrollPct) + 'vh'}
                    left={'85vw'}
                    position={PositionTypes.Absolute}
                    style={Transformer({
                        rotate: 5 * range1(scrollPct) + 'deg',
                        transformOrigin: '-25px -25px'
                    })}
                >
                    <RotatingGradient
                        colors={colors2}
                        style={{
                            ...BordererPresets.P50,
                            ...ShadowerPresets.Blur4,
                            ...Transformer({rotate: '-45deg'})
                        }}
                        width={5 * Math.sin(16 * scrollPct) + 15 + 'vw'}
                        height={'15vw'}
                        stepSize={2}/>
                </PositionBlock>
            </div>
        </Surface>
        <PositionBlock top={-range2(scrollPct) + 'vh'} left={0} right={0} style={{zIndex: 2}}
                       position={PositionTypes.Absolute}>
            <Surface height='initial' background='white' width='96%'
                     style={{
                         margin: '0 auto',
                         maxWidth: 1440,
                         ...BordererPresets._8,
                         ...Shadower({blur: 64, spread: 2, color: 'rgba(0, 0, 0, 0.6)'})
                     }}>
                <AboutUs/>
            </Surface>
        </PositionBlock>
        <FilledTestimonial {...TESTIMONIALS[0]} background='#22a77c'/>
        <ServicesExpertise/>
        <FilledTestimonial {...TESTIMONIALS[1]} background='#d64d57'/>
        <Team/>
        <FilledTestimonial {...TESTIMONIALS[2]} background='#bc8458'/>
        <ArcGis/>
        <FilledTestimonial {...TESTIMONIALS[3]} background='#e9b543'/>
        <Footer/>
    </div>
}
