import React from 'react';
import {
    Surface,
    Paragraph,
    FontStyle,
    TextAlign,
    useBreakpoint,
    BreakpointMedium, BaseProps, BordererPresets
} from '@spinglobal/react-foundations';
import {font__voicePrimary, font__voicePrimaryLeadin} from '../../styles/fonts';

export function TestimonialText({text}: {text: string}) {
    const finalText = `'${text}'`;
    return <Paragraph
        text={finalText}
        fontStyle={FontStyle.Italic} textAlign={TextAlign.Center}
        style={{...font__voicePrimary, padding: '36px 36px 6px 36px'}}
    />
}

export interface TestimonialNameProps {
    name: string;
    title: string;
}

export function TestimonialName({name, title}: TestimonialNameProps) {
    return <Surface style={{padding: '6px 36px 12px 36px'}}>
        <Paragraph text={name} style={font__voicePrimaryLeadin}/>
        <Paragraph text={title} style={font__voicePrimary}/>
    </Surface>

}

interface TestimonialProps extends BaseProps {
    text: string;
    name: string;
    title: string;
}

export function Testimonial({style = {}, classes = [], ...props}: TestimonialProps) {
    const {text, name, title} = props;
    const width = useBreakpoint() <= BreakpointMedium ? '90%' : '65%';
    return <Surface width='100%' style={{...style, overflow: 'auto'}}>
        <Surface background='white' width={width}
                 style={{margin: '48px auto 48px auto', ...BordererPresets._8}}>
            <TestimonialText text={text}/>
            <TestimonialName name={name} title={title}/>
        </Surface>
    </Surface>;
}
